
button.nav-button{position:relative;width:100%;overflow:hidden;color:#fff;background-color:#d6975c;text-shadow:0 0 1px #523214
}
button.nav-button.active{background-color:#a36429;text-shadow:#140c05
}
button.nav-button .button-main{text-transform:uppercase
}
button.nav-button .button-info{font-size:.6em
}
@media screen and (max-height:350px){
button.nav-button .button-main{font-size:.8em
}
button.nav-button .button-info{font-size:.5em
}
}
button.nav-button .chevron{-webkit-filter:drop-shadow(0 0 1px #523214);filter:drop-shadow(0 0 1px #523214);position:absolute;width:21px;top:50%;margin-top:-18px
}
button.nav-button .chevron svg path,button.nav-button .chevron svg line,button.nav-button .chevron svg circle{stroke:#fff;fill:transparent
}
button.nav-button .chevron.left{left:10.5px
}
button.nav-button .chevron.left svg{transform:scale(-1, 1)
}
button.nav-button .chevron.double-back{left:21px
}
button.nav-button .chevron.right{right:10.5px
}


