
.panel-outer{position:relative
}
.panel-outer .panel-inner{position:absolute;top:10px;left:10px;right:10px;bottom:0;overflow:hidden;background:#eee;border:1px solid #ddd;padding:.5em;color:#555;text-shadow:0 1px 1px #fff
}
@media screen and (max-height:550px){
.panel-outer .panel-inner{font-size:.9em
}
}
@media screen and (max-height:500px){
.panel-outer .panel-inner{font-size:.8em
}
}
@media screen and (max-height:400px){
.panel-outer .panel-inner{font-size:.7em
}
}
@media screen and (min-height:650px){
.panel-outer .panel-inner{font-size:1.2em
}
}


