
li.vote-option{position:relative;color:#afe2f6;background-color:#3cb8ea;text-shadow:0 0 1px #093c51;border-bottom:1px #81d1f1 solid
}
li.vote-option .label{position:absolute;top:.4em;left:3em
}
li.vote-option .extra{position:absolute;top:2.5em;left:4em;font-style:italic
}
li.vote-option .extra:before{content:'"'
}
li.vote-option .extra:after{content:'"'
}
li.vote-option .smiley{position:absolute;top:.2em;left:.2em;bottom:.2em;width:2em;-webkit-filter:drop-shadow(0 0 1px #093c51);filter:drop-shadow(0 0 1px #093c51)
}
li.vote-option .smiley svg path,li.vote-option .smiley svg line,li.vote-option .smiley svg circle{stroke:#afe2f6;fill:transparent
}
li.vote-option.active{color:#fff;background-color:#1381ad;text-shadow:0 0 1px #041a23
}
li.vote-option.active .smiley{-webkit-filter:drop-shadow(0 0 1px #000);filter:drop-shadow(0 0 1px #000)
}
li.vote-option.active .smiley svg path,li.vote-option.active .smiley svg line,li.vote-option.active .smiley svg circle{stroke:#fff;fill:transparent
}
@media screen and (min-height:550px){
li.vote-option .extra{font-size:.65em
}
}
@media screen and (max-height:550px){
li.vote-option .label{font-size:.9em
}
li.vote-option .extra{font-size:.6em;top:2.15em
}
}
@media screen and (max-height:500px){
li.vote-option .label{font-size:.8em
}
li.vote-option .extra{font-size:.55em;top:2.1em
}
li.vote-option .smiley{left:.1em
}
}
@media screen and (max-height:450px){
li.vote-option .label{font-size:.7em
}
li.vote-option .extra{font-size:.5em;top:2.05em
}
li.vote-option .smiley{left:0
}
}
@media screen and (max-height:400px){
li.vote-option .label{font-size:.6em
}
li.vote-option .extra{font-size:.4em;top:2em
}
li.vote-option .smiley{left:-0.2em
}
}


