
*,ul,li,button,input,body{margin:0;padding:0;border:0;font-family:Helvetica,Arial,sans-serif;font-size:24px
}
html,body,div#app{margin:0;padding:0;border:0;position:relative;width:100%;height:100%;background-color:#888;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
#app .page.slide-right-enter{left:100%
}
#app .page.slide-right-enter-active{transition:left .3s
}
#app .page.slide-right-leave-active{left:-100%;transition:left .3s
}
#app .page.slide-left-enter{left:-100%
}
#app .page.slide-left-enter-active{transition:left .3s
}
#app .page.slide-left-leave-active{left:100%;transition:left .3s
}


